import PopularCotentsListPanel from '../../main/ui_components/PopularCotentsListPanel';

// service
import VideoService from '../../../services/video.service';
import LiveService from '../../../services/live.service';
import LiveViewService from '../../../services/live_view.service';
// store
import { CHANNEL_INFO, LIVE_INFO } from '../../../pages/live/store/live.store';

let $livePanel = null;
export default (() => {
    let $firstVideoPanel = null;
    let uiMainVideoListPanel = null;
    let uiJoinVideoListPanel = null;
    let _userSeq = null;
    const init = (userSeq) => {
        _userSeq = userSeq;
        uiMainVideoListPanel = new PopularCotentsListPanel();
        uiJoinVideoListPanel = new PopularCotentsListPanel();
    
        $firstVideoPanel = $('#first-video');
        $livePanel = $('#main-live');
        $livePanel.hide();
        uiMainVideoListPanel.init('#main-video-list');
        uiJoinVideoListPanel.init('#main-join-video-list');
    }
    
    const initData = async () => {
        // 대표 비디오 설정
        if (CHANNEL_INFO.get().first_vod) {
            const resFirstVideo = VideoService.getVideoInfo(
                CHANNEL_INFO.get().first_vod
            )
            .then(res => {
                const videoInfo = res.data.vod;
                const videoFilePath = res.data.vod_path.find(k => k.vod_type === "MP4")
                const combinedVideoPath = videoFilePath.domain + videoFilePath.path;
                $firstVideoPanel.append(
                    createFirstVideoCard(videoInfo.title, videoInfo.description, combinedVideoPath)
                );
    
            })
        }

        // 현재 라이브
        getCurrentLive(_userSeq);

        // 최근 영상 리스트
        getMainVideoList();

        // 최근 참여 비디오 리스트
        getJoinVideoList();

    }

    const getMainVideoList = async () => {
        const resVideos = await VideoService.getVideoList(0, 4, "01", _userSeq);
        const videoList = resVideos.data.vods;
        uiMainVideoListPanel.appendList(videoList, '/video', 'vod_seq');
    }

    const getJoinVideoList = async () => {
        const resJoinVideos = await LiveService.getJoinList_Video(
            CHANNEL_INFO.get().channel_seq, 0, 20
        );
        const joinVideoList = resJoinVideos.data.vod_joins;
        uiJoinVideoListPanel.appendList(joinVideoList, '/video', 'vod_seq', 4);
    }

    const pauseFirstVideo = () => {
        const firstVideo = $('.first-video-card video');
        if(firstVideo.get(0)) {
            firstVideo.get(0).pause()
        }
    }


    const resumeFirstVideo = () => {
        const firstVideo = $('.first-video-card video');
        if (firstVideo.get(0)) {
            firstVideo.get(0).play()
        }
    }

    return {
        init, initData, getJoinVideoList, pauseFirstVideo, resumeFirstVideo
    }
})()

const getCurrentLive = async (userSeq) => {
    const resGetLive = await LiveService.getRecommendLive(userSeq, 0, 1);
    if (!resGetLive.data || !resGetLive.data.live || resGetLive.data.live.length === 0) return;
    $livePanel.show();
    const { live_seq, thumbnail_path, title, live_cnt } = resGetLive.data.live[0];
    $livePanel.find('.contents').append(
        createLiveCard(
            live_seq,
            thumbnail_path,
            title,
            live_cnt
        )
    )

    $('.current-live-card').click(e => {
        const liveSeq = $(e.currentTarget).attr('data-live-seq');
        location.href = `/live/${liveSeq}`
    })
};

const createFirstVideoCard = (title, desc, src) => {
    return `
    <div class="first-video-card">
        <div class="video-container">
            <video class="first-video" src='${src}' autoplay controls>
        </div>
        <div class="contents-container">
            <div class="title-container fs-n bold">
                ${title}
            </div>
            <div class="desc-container fs-xs fc-light-black">
                ${desc}
            </div>
        </div>
    </div>
    `
}

const createLiveCard = (live_seq, thumbnail, title, viewCnt) => {
    return `
    <div class="current-live-card" data-live-seq="${live_seq}">
        <div class="thumbnail-container${thumbnail ? '' : ' _blank'}">
            ${thumbnail ?
            `<img src='${thumbnail}'>`
            : ''}
        </div>
        <div class="contents-container">
            <div class="status-container"><span class="onair-status on fs-xxs bold fc-white">ON AIR</span></div>
            <div class="title">${title}</div>
            <div class="view-count-container">
                <img src="/static/cojam/res/ic-current-viewer.png">
                <span class="fs-xxs">${viewCnt}</span>
            </div>
        </div>
    </div>
    `
}