import Tab from '../../common_ui/tab';
import ChannelInfo from '../../common_ui/ChannelInfo';
import uiMainPanel from './ui_components/channel.ui.main';
import uiVideoListPanel from './ui_components/channel.ui.video_list_panel';
import uiJoinVideoPanel from './ui_components/channel.ui.join_video_list';
import JoinButton from '../live_view/ui_components/live_view.ui.join_button';
import uiJoinVideoPopup from './ui_components/channel.ui.join_video_popup';
import CardGenerator from '../../common_ui/card_generator';

import LiveService from '../../services/live.service';
import LiveViewService from '../../services/live_view.service';

import { CHANNEL_INFO } from '../live/store/live.store';
import { USER_INFO } from '../live_view/store/live_view.store';

let uiTabContainer = null;
let uiChannelInfo = null;
let uiCurrentLivePanel = null;
let uiJoinVideoButton = null;

let uiSortMenu = null;
let _order = null;
const ORDER_CODE = [
    "01", "02", "03"
]

$(document).ready( async () => {
    const userSeq = getUserSeq_FromPath();
    const resGetUserInfo = await LiveService.getChannelInfo(userSeq);
    CHANNEL_INFO.set(resGetUserInfo.data.channel_info);

    uiChannelInfo = new ChannelInfo();
    uiChannelInfo.init('.channel-info', true);
    uiMainPanel.init(userSeq);
    uiCurrentLivePanel = $('#current-live-info');
    uiJoinVideoPanel.init();
    uiTabContainer = new Tab();


    uiTabContainer.init('#tab-panel-video-list', [
        uiMainPanel.resumeFirstVideo,
        () => {
            uiVideoListPanel.init(userSeq);
            uiMainPanel.pauseFirstVideo()
        },
        () => { uiJoinVideoPanel.getJoinList(); uiMainPanel.pauseFirstVideo()}
    ]);
    
    uiJoinVideoButton = new JoinButton(
        i18next.t('join_with_video'),
        '동영상 참여취소',
        onClick_JoinVideo,
        onClick_CancelJoinVideo
    );
    uiJoinVideoButton.init('#btn-join-video');
    if(USER_SESSION) {
        uiJoinVideoButton.setEnable(true);
    } else {
        uiJoinVideoButton.setEnable(false);
    }
    uiJoinVideoPopup.init(confirmJoinVideo);

    _order = ORDER_CODE[0]

    bindEventHandler();
    
    uiMainPanel.initData();
    // TODO : 현재 userSeq와 channelSeq가 똑같은데, 서로 다른 값을 가지게 될 경우
    // case1: userSeq를 이용하여 channelSeq를 구하는 방법
    // case2: channelSeq를 이용하여 userSeq를 구하는 방법
    // 이 필요해짐.
    uiChannelInfo.setData(userSeq, CHANNEL_INFO.get().channel_seq);
    // uiVideoListPanel.getList("01", true);
    if (CHANNEL_INFO.get().thumbnail_path) {
        $('.channel-banner-container').css('background', `url('${CHANNEL_INFO.get().thumbnail_path}')`);
    }
})

const getUserSeq_FromPath = () => {
    let paths = window.location.pathname.split('/');
    return paths[2];
};


const bindEventHandler = () => {

    $('#main-video-list .more').click((e) => {
        uiTabContainer.changeTab("tab-contents-video")
    })
    $('#main-join-video-list .more').click((e) => {
        uiTabContainer.changeTab("tab-contents-join-video")
    })
}

const onClick_JoinVideo = () => {
    uiJoinVideoPopup.open();
};
const onClick_CancelJoinVideo = () => { };

const confirmJoinVideo = async data => {
    const channelSeq = CHANNEL_INFO.get().channel_seq;
    const userSeq = USER_SESSION.user_seq;
    let videoSeq = null;
    if (data.vod) {
        videoSeq = data.vod.vod_seq;
    } else {
        videoSeq = data.vod_seq;
    }

    LiveViewService.joinVideo(channelSeq, userSeq, videoSeq)
        .then(res => {
            const { status, message } = res;
            if (status !== 200) {
                alert(message);
                return;
            }
            uiMainPanel.getJoinVideoList();
            uiJoinVideoPanel.getJoinList();
        })
        .catch(err => {
            console.log(err)
        });
};
