import CardGenerator from '../../../common_ui/card_generator';
import PrettyDate from '../../../utils/PrettyDate';
import ClacDuration from '../../../utils/ClacDuration';

class PopularCotentsListPanel {
    constructor() {
        this.$list_panel = null;
        this.$contents = null;
        this.liveMode = false;
    }

    init(selectQuery, liveMode) {
        this.$list_panel = $(selectQuery);
        this.$contents = this.$list_panel.find('.contents');
        this.liveMode = liveMode;
    }

    clearList() {
        this.$contents.empty();
    }

    async appendList(list, baseURL, seqName, length) {

        if (!list || list.length === 0) return;
        length = length ? length : list.length;
        this.clearList();
        list.map( (data, index) => {
            if(index >= length)  return;
            
        // list.map((data, index) => {
            const { title='', nick_name, profile_img_url, thumbnail_path, created_at, duration, view_cnt, user_seq } = data;
            const seq = data[seqName];
            const url = `${baseURL}/${seq}`
            if(this.liveMode) {
                const live_cnt = data.live_cnt
                this.addCard(
                    CardGenerator.createLiveCard({
                        title,
                        name: nick_name,
                        url,
                        avatar_url: profile_img_url,
                        thumbnail: thumbnail_path,
                        live_cnt,
                        user_seq
                    })
                )
            } else {
                this.addCard(
                    CardGenerator.createCard({
                        title,
                        name: nick_name,
                        url,
                        avatar_url: profile_img_url,
                        thumbnail: thumbnail_path,
                        update: PrettyDate(created_at),
                        duration: ClacDuration(duration),
                        view_cnt,
                        user_seq
                    })
                )
            }
        });
        CardGenerator.bindEventHandlerAll();
        // CardGenerator
    }

    addCard(cardElement) {
        this.$contents.append(cardElement);
    }
}

export default PopularCotentsListPanel;